n  box-shadow: 10px 20px 20px rgba(0, 0, 0, 0.459);\n  text-align: center;\n}\n\n.card img {\n  width: 100%;\n  height: 200px;\n  border-radius: 2rem;\n  object-fit: cover;\n}\n\n.card h2 {\n  margin-block-start: 0;\n  font-family: Poppins, sans-serif;\n}\n\n.card p {\n  font-family: Quicksand, sans-serif;\n}\n\n.card p a {\n  color: rgb(30, 143, 219);\n  padding: 1rem;\n  font-weight: 700;\n}\n\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: orange;\n\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n\n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n  z-index: 9999;\n}\n\n#overlay {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background: rgba(0, 0, 0, 0.616);\n  z-index: 1;\n}\n\n#loading {\n  position: fixed;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  font-size: 3rem;\n  color: #918d8d;\n  z-index: 9999;\n}\n\n.spinner {\n  margin-inline: auto;\n  border: 6px solid rgba(0, 0, 0, 0.1);\n  width: 66px;\n  height: 66px;\n  border-radius: 50%;\n  border-left-color: #09f;\n  animation: spin 1s ease-out infinite;\n}\n\n.text-loading {\n  margin-block-start: 1rem;\n}\n\n@keyframes spin {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n.detail_restaurant {\n  background-color: #13181d;\n  color: white;\n  padding: 9rem 6rem;\n}\n\n.detail_restaurant h2 {\n  margin: 0;\n  font-size: 2.4rem;\n  font-family: Poppins, sans-serif;\n  text-align: center;\n}\n\n.location_restaurant {\n  text-align: center;\n  font-family: Quicksand, sans-serif;\n  color: orange;\n}\n\n.restaurant_info {\n  display: flex;\n  justify-content: space-between;\n  padding: 2rem;\n  margin-block: 6rem;\n  border-radius: 2rem;\n  background-color: orange;\n  box-shadow: 0 0 25px black;\n  color: #13181d;\n  font-family: Quicksand, sans-serif;\n  font-weight: bold;\n}\n\n.restaurant_info p {\n  margin: 0;\n  max-width: 650px;\n  padding-inline-end: 3rem;\n}\n\n.info_image img {\n  width: 300px;\n  border-radius: 2rem;\n}\n\n.menus h3 {\n  text-align: center;\n  font-family: Poppins, sans-serif;\n  font-size: 2rem;\n}\n\n.restaurant_menus {\n  display: flex;\n  justify-content: center;\n}\n\n#menus {\n  padding: 2rem;\n  margin-block: 3rem;\n}\n\n#menus h4 {\n  font-family: Poppins, sans-serif;\n  font-weight: bold;\n  font-size: 1.6rem;\n  color: orange;\n  text-align: center;\n}\n\n#menus ul {\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  padding: 0;\n  justify-content: center;\n}\n\n#menus ul li {\n  margin: 20px;\n  text-align: center;\n  font-weight: 700;\n  font-family: Quicksand, sans-serif;\n  background-color: rgb(241, 162, 14);\n  color: white;\n  list-style: none;\n  padding: 1rem;\n}\n\n.reviews_restaurant {\n  padding: 3rem;\n}\n\n.reviews_restaurant h4 {\n  font-family: Poppins, sans-serif;\n  font-size: 1.6rem;\n  text-align: center;\n}\n\n.review_form {\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  justify-content: space-between;\n  align-items: start;\n  padding-block-start: 6rem;\n}\n\n.review_form form {\n  display: grid;\n  gap: 3rem;\n  width: 70%;\n}\n\n.review_form form textarea {\n  min-width: 100px;\n  height: 100px;\n  padding: 0.5rem;\n}\n\n.review_form form input {\n  min-width: 100px;\n  padding: 0.8rem;\n  font-size: 1rem;\n}\n\n.review_form form button {\n  min-width: 100px;\n  padding: 1rem;\n  background-color: orange;\n  border: none;\n  font-family: Quicksand, sans-serif;\n  font-weight: 700;\n  cursor: pointer;\n  color: #13181d;\n}\n\n.review_form form button:hover {\n  background-color: rgb(235, 157, 12);\n}\n\n.review_form #reviews {\n  margin-inline-start: 2rem;\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  background-color: orange;\n  padding: 1rem;\n  border-radius: 1rem;\n  overflow-y: scroll;\n  height: 600px;\n  width: 600px;\n  align-items: start;\n}\n\n#reviews .review {\n  background-color: #13181d;\n  color: white;\n  margin: 1rem;\n  padding: 1rem;\n  border-radius: 1rem;\n  box-shadow: 0 0 15px #13181d;\n  height: auto;\n}\n\nfooter p {\n  margin: 0;\n  padding: 2rem;\n  text-align: center;\n  color: white;\n  background-color: #13181dfa;\n  font-family: Quicksand, sans-serif;\n}\n\n@media (max-width: 910px) {\n  .info_image img {\n    width: 250px;\n  }\n\n  .info_description {\n    font-size: 0.9rem;\n  }\n\n  .detail_restaurant {\n    padding-inline: 2rem;\n  }\n\n  .restaurant_menus {\n    flex-direction: column;\n  }\n\n  .review_form #reviews {\n    grid-template-columns: repeat(1, 1fr);\n    width: 400px;\n  }\n}\n\n@media (max-width: 790px) {\n  .my-header {\n    padding-inline: 1rem;\n  }\n\n  .title-catalog {\n    font-size: 1.2rem;\n  }\n\n  .restaurant_info {\n    flex-direction: column;\n    justify-content: center;\n    margin: auto;\n  }\n\n  .info_description p {\n    padding: 0;\n  }\n\n  .reviews_restaurant {\n    padding: 0rem;\n  }\n}\n\n@media (max-width: 670px) {\n  .navbar .navbar-menu {\n    display: none;\n  }\n\n  .navbar .toggle-menu {\n    display: flex;\n    padding-inline-end: 1rem;\n  }\n\n  .hero-text h1 {\n    font-size: 1.4rem;\n  }\n\n  .hero-text p {\n    font-size: 1rem;\n  }\n\n  .cta_description h1 {\n    font-size: 1.5rem\n  }\n\n  .catalog-section {\n    padding: 2rem;\n  }\n\n  .detail_restaurant {\n    padding-inline: 2rem;\n  }\n\n  .review_form #reviews {\n    width: 250px;\n  }\n}\n\n@media (max-width: 520px) {\n  .review_form {\n    grid-template-columns: repeat(1, 1fr);\n    justify-items: center;\n  }\n\n  .review_form #reviews {\n    margin: 6rem 0rem;\n  }\n  \n}\n\n@media (max-width: 420px) {\n  .my-header {\n    padding-inline: 0;\n  }\n\n  .my-header .title-app {\n    font-size: 1rem;\n  }\n\n  .hero-text {\n    padding: 1rem;\n  }\n\n  .title-catalog {\n    font-size: 1rem;\n  }\n\n  .info_image img {\n    width: 180px;\n  }\n\n  .info_description p {\n    font-size: 0.7rem;\n  }\n\n  #menus ul {\n    display: grid;\n    grid-template-columns: repeat(2, 1fr);\n    padding: 0;\n    justify-content: center;\n  }\n}\n\n@keyframes slideMenu {\n  from {\n    transform: translateX(-100%);\n    opacity: 0;\n  }\n  to {\n    transform: translateX(0);\n    opacity: 1;\n  }\n}\n\n.navbar-menu.navbar-expand {\n  position: fixed;\n  left: 0;\n  top: 65px;\n  width: 100%;\n  height: 50vh;\n  z-index: 1000;\n  background-color: #13181d;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  animation: slideMenu 0.5s ease forwards;\n  padding: 0;\n  margin: 0;\n}\n\n.skip-link {\n  position: absolute;\n  left: -9999px;\n  top: 0;\n  z-index: 99999;\n  text-decoration: none;\n}\n\n.skip-link:focus {\n  left: 50%;\n  background-color: orange;\n  padding: 10px;\n  color: #13181d;\n}\n\n#main-content:focus {\n  outline: 3px solid #ff0;\n}\n\n.navbar ul li a:focus {\n  border-bottom: 2px solid orange;\n  transition: none;\n}\n"],"sourceRoot":""}]);
